@media (max-width: 1605px) {
  .page-section {
    // section type
    &__hero {
      height: 700px;
    }

    &__cases {
      // height: 1500px;
    }

    &__team {
      height: 900px;

      .page-section__pipe {
        width: 1605px;
        top: 305px;
        left: -100px;
        max-width: none;
      }
    }
  }
}

@media (max-width: 1440px) {
  .page-section {
    &__team {
      .page-section__pipe {
        left: 0;
      }
    }
  }
  .composit-pipe {
    &--1 {
      z-index: -1;

      width: 903px;
      top: 197px;
      left: -193px;
    }
    &--2 {
      // display: none;
      z-index: 4;

      width: 361.18px;
      top: 189px;
      left: 710.495px;
    }
    &--3 {
      z-index: 5;

      width: 541.8px;
      top: 323px;
      left: 872.78px;
    }
    &--4 {
      z-index: -1;
      width: 928.8px;
      top: -247px;
      left: 514.8px;
    }
  }
  .team-card {
    &-wrap {
      &--1 {
        left: 25px;
        top: 240px;
      }
    }
  }
}

@media (max-width: 1420px) {
  .case-card {
    &__pipe-2 {
      top: 60px;
    }
    &--2 {
      &::after {
        top: 236px;
        width: 125px;
      }
    }
  }
}

@media (max-width: 1200px) {
  .page-section {
    // section type
    &__hero {
      height: 700px;

      .page-section__pipe {
        width: 528.8px;
      }
    }

    // &__cases {
    //   // height: 1500px;
    // }

    &__news {
      .page-section__pipe {
        top: -160px;
      }
    }

    &__team {
      height: 900px;
    }
  }

  .case-card {
    &__image {
      width: 50%;
    }

    &__pipe-1 {
      top: 300px;
    }
    &__pipe-2 {
      top: 315px;
    }
    &__pipe-3 {
      top: 460px;
    }

    &--2 {
      &::after {
        top: 460px;
        width: 90px;
        height: 150px;
      }
    }
  }
}

@media (max-width: 992px) {
  .main-header {
    &__phone {
      margin-right: 0;
    }
    &__request-call {
      display: none !important;
    }
  }

  .page-section {
    &__hero {
      &-video-link {
        display: flex;
        align-items: center;

        margin-top: 30px;
        margin-bottom: 30px;

        margin-left: 0;
      }

      .dd-button {
        width: 100%;
      }
    }
  }

  .contact-form {
    flex-wrap: wrap;

    &__splash {
      background-position: bottom;
      background-image: url("../assets/contact-form-mobile.png");

      &-title {
        font-size: 3rem;
      }
    }

    &__content {
      padding: 30px 15px;
      &-title {
        font-size: 1rem;
      }

      .dd-button {
        width: 100%;
      }
    }
  }
}

@media (max-width: 768px) {
  .dd-containe {
    padding: 0 30px;
  }

  .main-logo {
    width: 150px;
  }

  .dd-button {
    font-size: 0.875rem;
    padding-left: 20px;
    padding-right: 20px;
  }

  .dd-popup {
    &-wrap {
      padding: 30px;
    }

    &--form {
      padding: 50px 25px;
    }

    &--video {
      iframe {
        border-radius: 10px;
      }
    }

    &__footer {
      text-align: right;
      span {
        display: none;
      }
      .dd-button {
        width: 100%;
      }
    }
  }

  .page-section {
    padding: 10px 0;
    &__title {
      font-size: 1.5rem;
    }
    // section type
    &__hero {
      &-title {
        font-size: 1.5rem;
      }
      &-subtitle {
        font-size: 1.125rem;
      }
      &-partners {
        display: flex;
        justify-content: center;
        img {
          height: 21.85px;

          &:nth-child(1) {
            width: 83.35px;
          }
          &:nth-child(2) {
            width: 107.79px;
          }
          &:nth-child(3) {
            width: 80.48px;
          }
        }
      }

      .page-section__pipe {
        left: -40px;
        // width: 450px;
        max-width: none;
      }
    }

    &__parallax {
      padding: 100px 0;
      &-title {
        font-size: 1.5rem;
        margin-bottom: 1.875rem;
      }
      &-content {
        font-size: 0.875rem;
      }

      .container {
        padding: 0;
      }
    }

    &__news {
      padding-top: 100px;

      &-header {
        margin-bottom: 1.875rem;
      }
      &-content {
        margin-top: -100px;
      }

      .page-section__pipe {
        top: -40px;
        width: 900px;
        max-width: none;
        height: 1800px;
        left: -50px;
      }
    }

    &__team {
      height: auto;
      &-title {
        font-size: 1.5rem;
      }
      &-text {
        font-size: 0.875rem;
      }

      .page-section__pipe {
        top: 250px;
        left: -150px;
        width: 100%;
        height: 1350px;
        min-width: 1000px;
        z-index: 2;
        transform: rotateZ(11deg) rotateX(-12deg);
      }
    }
  }

  .composit-pipe {
    &-wrap {
      top: -1420px;
      // left: -375px;
    }

    &--1 {
      top: 450px;
      left: 0;
    }
    &--2 {
      display: none;
    }
    &--3 {
      display: none;
    }
    &--4 {
      left: 105.8px;
    }
  }

  .small-cases-swiper {
    margin-right: -30px;
    .swiper-slide {
      padding: 0;
      padding-right: 30px;
    }
  }
  .small-case-card {
    padding: 0;
  }

  .case-card {
    &-wrap {
      width: auto;
      margin-left: -10px;
      margin-right: -10px;
      &--1 {
        z-index: 5;
        .case-card__image {
          width: 270px;
          right: -130px;
          bottom: 35px;
        }
      }
      &--2 {
        .case-card__image {
          bottom: -50px;
          right: -110px;
        }
      }
      &--3 {
        .case-card__image {
          width: 178px;
          bottom: -30px;
          right: -15px;
        }
      }
    }

    min-height: 650px;
    padding: 30px 20px;
    margin-bottom: 20px;

    &__tags {
      display: flex;
      flex-wrap: wrap;
    }

    &__tag {
      font-size: 0.625rem;
    }

    &__title {
      font-size: 3rem;
    }

    &__description {
      font-size: 1.125rem;
    }

    &__content {
      width: 100%;
      .row {
        .col-md-6 {
          &:nth-child(3) {
            .case-card__step-content {
              width: 60%;
            }
          }
          &:nth-child(4) {
            .case-card__step-content {
              width: 60%;
            }
          }
        }
      }
    }

    &__steps {
      width: 100%;
    }

    &__step {
      &-title {
        font-size: 1.125rem;
      }
      &-content {
        font-size: 0.875rem;
      }
    }

    &__image {
      width: auto;
    }

    &__pipe-1 {
      display: none;
    }
    &__pipe-2 {
      left: -94px;
      top: -75px;
      width: 1400px;
      max-width: none;
    }
    &__pipe-3 {
      max-width: none;
      width: 1351px;
      height: 1190px;
      top: 250px;
      left: -50px;
    }

    &--1 {
      position: relative;
    }

    &--2 {
      &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        background: linear-gradient(90deg, #fdc227 0, #fda327 100%);
        width: 145px;
        height: 300px;
        border-top-left-radius: 20px;
        border-top-right-radius: 50%;
        z-index: 3;
      }
    }

    &--3 {
      &::after {
        content: "";
        position: absolute;
        bottom: 0;
        right: 0;
        background: #370084;
        width: 65px;
        height: 80px;
        border-bottom-right-radius: 20px;
        z-index: 3;
      }
    }

    .dd-button {
      width: 100%;
      text-align: center;
    }
  }

  .parallax-element {
    &--1 {
      top: 5px;
      left: -40px;

      transform: translateY(100%);
    }
    &--2 {
      top: 5px;
      left: 0;

      transform: translateY(100%);
    }
    &--3 {
      top: 40px;
      right: -40px;

      transform: translateY(100%);
    }
    &--4 {
      top: 405px;
      right: 0;

      transform: translateY(100%);
    }
    &--5 {
      top: 485px;
      right: 55px;
      width: 60px;

      transform: translateY(100%);
    }
    &--6 {
      width: 60px;
      bottom: -100px;
      left: 0;
      z-index: 2;

      transform: translateY(100%);
    }
    &--7 {
      bottom: -105px;
      left: -45px;
      z-index: 3;

      transform: translateY(100%);
    }
  }

  .news-card {
    &-wrap {
      &--1 {
        height: auto;
        margin-bottom: 20px;
      }
      &--2 {
        height: auto;
        margin-bottom: 20px;
      }
      &--3 {
        height: auto;
        margin-bottom: 20px;
      }
    }

    padding: 20px 20px;

    width: auto !important;
    margin-left: -10px;
    margin-right: -10px;

    &--vertical {
    }
    &--horizontal {
      .news-card__text {
        width: 100%;
      }
    }
    &--video {
      padding: 0;
      height: 100%;

      img {
        border-radius: 10px;
      }
    }

    &__title {
      font-size: 1.125rem;
    }
    &__text {
      font-size: 0.875rem;
    }
    &__logo {
      left: 20px;
    }
    &__link {
      display: block;
    }

    &--3 {
      min-width: 0;
    }
  }

  .team-card {
    &-wrap {
      &--1 {
        // top: 300px;
        // left: 0;
        position: static;
        .team-card__image {
          margin-left: 20px;
        }
      }

      &--2 {
        // top: 723px;
        // left: 0;
        position: static;
        .team-card__image {
          margin-left: 20px;
        }
      }

      &--3 {
        // top: 1114px;
        // left: 0;
        position: static;
        .team-card__image {
          margin-left: 20px;
          margin-bottom: -30px;
        }
      }
    }

    width: 100%;
    padding: 0;

    &__image {
      width: 260px;
    }
    &__content {
      width: auto;
      margin-left: -10px;
      margin-right: -10px;

      padding: 20px;

      &-name {
        font-size: 1.125rem;
      }
      &-position,
      &-team {
        font-size: 0.875rem;
        display: inline-block;
      }
    }
  }

  .parallax-element {
    max-width: 100px;
  }

  .contact-form {
    &-wrap {
      width: auto;
      margin-left: -10px;
      margin-right: -10px;
    }
  }
}

@media (max-width: 576px) {
  .dd-button {
    font-size: 0.875rem;
    padding-left: 20px;
    padding-right: 20px;
  }
  .main-logo {
    width: 110px;
  }

  .case-card {
    &__pipe-3 {
      max-width: none;
      width: 1351px;
      height: 1190px;
      top: 460px;
      left: -90px;
    }
  }
}

@media (max-width: 390px) {
  .case-card {
    &__pipe-3 {
      width: 1351px;
      height: 1464px;
      left: -90px;
    }
  }
}
